<template>
  <div class="col-lg-4 col-md-6 col-sm-12 col-xs-12 mt-2">
   
  <div class="card mt-3">
    <div class="card-header">
      <h2>{{producto.nombre}}</h2>
    </div>
      <div class="card-body">
        <p>{{producto.descripcion}}</p>
      </div>

      <button class="btn" :class="producto.seleccionado ? 'btn-success' : 'btn-secondary'" @click="seleccionarProducto(producto.nombre)">Seleccionar</button>
   </div>


  </div>
</template>

<script>
export default {
  name:'Producto',
  data(){
    return{
      productosSeleccionados:[]
    }
  },
  props:{
    producto:Object
  },
  methods:{
    cambiarEstado(){
     this.producto.seleccionado = !this.producto.seleccionado;
    },
    seleccionarProducto(nombre){
    this.cambiarEstado();
    this.$emit('seleccionarProducto', nombre, this.producto.seleccionado);
    }
  }
}
</script>