<template>
  <div class="container text-center">

      <div class="container mt-4">
       <a class="btn btn-success"
        @click="verProductosSeleccionados()"
      :href="'https://api.whatsapp.com/send?phone=+573115643736&text=Para cotizar estos productos: ' + '%20' + mensaje"
      target="_blank"><span class="icon-Whatsapp"></span> Cotizar los {{cantidadSeleccionados}} productos seleccionado</a>
      </div>


      <div class="container mt-3">
        <h4>Seleccione los productos a cotizar:</h4>
      </div>

    <div class="row mb-4">
       <TarjetaProducto v-for="dato in datos" :key="dato.nombre" :producto="dato" :id="dato.id" @seleccionarProducto="seleccionarProducto"></TarjetaProducto> 
    </div>

 
   
  </div>
</template>

<script>
import TarjetaProducto from '@/components/TarjetaProducto';
export default {
  name:'cotizacion',
  created(){
  this.obtenerTodos();
  },
  data(){
  return{
    datos:[{'nombre':'Prueba1', 'descripcion':'prueba', 'seleccionado':false}],
    productosSeleccionados:[],
    cantidadSeleccionados:0,
    mensaje:''
  }
  },
  components:{
    TarjetaProducto
  },
  methods:{
    async obtenerTodos(){
    let urlApi = 'http://localhost:3000/producto';
    const respuesta = await fetch(urlApi);
    const datos = await respuesta.json();
    this.datos = datos;
    },
    quitarProductoSeleccionado(nombre){
     var i = this.productosSeleccionados.indexOf( nombre );
 
      if ( i !== -1 ) {
          this.productosSeleccionados.splice( i, 1 );  
          this.cantidadSeleccionados --;
      }
    },
    agregarProductoSeleccionado(nombre){
      this.productosSeleccionados.push(nombre)
      this.cantidadSeleccionados ++;
    },
    seleccionarProducto(nombre, estado){

      if(!estado){
      this.quitarProductoSeleccionado(nombre)

      }else{
       this.agregarProductoSeleccionado(nombre);
      }


    },
    verProductosSeleccionados(){
     if(this.cantidadSeleccionados >0){
       this.mensaje = ''
        this.productosSeleccionados.forEach((producto)=>{
        this.mensaje += producto + '. ';
      })
      console.log(this.mensaje)
     }else{
       alert('No hay productos seleccionados')
     }
    }
  }
}
</script>